import { IoIosArrowForward } from "react-icons/io";
import PaperDropdown from "./PaperDropdown";
import React from "react";
import { withPrefix } from "gatsby";          // ⟵ add this
import { useTextColors } from "./ColorContext";

interface LinksProps {
  arxivUrl: string;
  pdfUrl: string;                // may be external or internal
  otherUrls: [string, string][];
}

const isExternal = (u: string) => /^https?:\/\//i.test(u) || u.startsWith("mailto:");
const needsPrefix = (u: string) => u.startsWith("/") && !u.startsWith("//");
const withMaybePrefix = (u: string) => (needsPrefix(u) ? withPrefix(u) : u);

const LinkGroup: React.FC<LinksProps> = ({ arxivUrl, pdfUrl, otherUrls }) => {
  const { linkColor } = useTextColors();

  return (
    <div className="flex justify-center mt-4 mb-12">
      {/* If PaperDropdown uses pdfUrl directly, pass the prefixed version */}
      <PaperDropdown
        arxivUrl={arxivUrl}
        pdfUrl={withMaybePrefix(pdfUrl)}
      />

      {otherUrls.map(([url, text], index) => {
        const href = withMaybePrefix(url);
        const external = isExternal(href);
        return (
          <a
            key={index}
            href={href}
            className="underline-offset-small inline-flex items-center text-base mr-6"
            style={{ color: linkColor }}
            {...(external ? { target: "_blank", rel: "noopener noreferrer" } : {})}
          >
            {text} <IoIosArrowForward className="ml-1" />
          </a>
        );
      })}
    </div>
  );
};

export default LinkGroup;
